function onMouseMove(event) {
  mouseMovement.x = (event.clientX / window.innerWidth) * 2 - 1;
  mouseMovement.y = - (event.clientY / window.innerHeight) * 2 + 1;
  
}

function setupTween (position, target, duration)
{
    //TWEEN.removeAll();    // remove previous tweens if needed

    new TWEEN.Tween (position)
        .to (target, duration)
        .easing (TWEEN.Easing.Exponential.InOut)
        .onUpdate (
            function() {
                // copy incoming position into capera position
                camera.position.copy (position);
            })
        .start();
   
}

function setupTweenRotate (position, target, duration)
{
    TWEEN.removeAll();    // remove previous tweens if needed

    new TWEEN.Tween (rotation)
        .to (target, duration)
        .easing (TWEEN.Easing.Exponential.InOut)
        .onUpdate (
            function() {
                // copy incoming position into capera position
                camera.rotation.copy (position);
            })
        .start();
}

